
import { defineComponent, PropType } from 'vue'

export default defineComponent({
  props: {
    text: {
      required: true,
      type: String as PropType<string>
    },
    href: {
      type: String as PropType<string>,
      default: ''
    },
    icon: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    white: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    big: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    border: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  }
})
